@import './assets/styles/fonts.css';

html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: 'outfit-r';
}



.icon-error {
  display: inline-block;
  width: 24px;
  height: 24px;
  background-color: #f0f0f0;
  border-radius: 4px;
  position: relative;
  text-align: center;
  font-size: 10px;
  line-height: 24px;
  color: #999;
  overflow: hidden;
}

.design-sys-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 54px;
  font-family: 'cera-m';
  line-height: 90%;
  font-size: 18px;
  color: #1B2130;
  background-color: #2BDDB5;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.design-sys-button:hover {
  background-color: #22B090;
  transform: translateY(-2px);
}


